.users {
  margin: 2rem auto;
  width: 90%;
  max-width: 40rem;
}

.users ul {
  list-style: none;
  padding: 1rem;
}

.users li {
  border: 1px solid #ccc;
  margin: 0.5rem 0;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.title {
  color: aqua;
  display: flex;
  align-items: center;
  height: 100%;
  font-size: 25px;
  padding-left: 40px;
}
.color{
    color: red;
}